import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ExamsService } from 'src/app/services/exams.service';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-exams2',
  templateUrl: './exams2.page.html',
  styleUrls: ['./exams2.page.scss'],
})
export class Exams2Page implements OnInit {
  form: FormGroup
  constructor(
    private router: Router,
    private examSrv: ExamsService
    ) { }

  ngOnInit() {

    this.initialFormLogin()
  }

  initialFormLogin(){
    this.form = new FormGroup({
      mes:          new FormControl(null,[Validators.required]),
      testosterona: new FormControl(null,[Validators.required]),
      igf1:         new FormControl(null,[Validators.required]),
      sdhea:        new FormControl(null,[Validators.required]),
      cortisol:     new FormControl(null,[Validators.required]),
      vitamina:     new FormControl(null,[Validators.required])
    });
  }

  goToThree(){
    this.router.navigate(['exams3'])
  }

  submit(){
    console.log(this.form.value);
    this.examSrv.insertPerfilHormonalUser(this.form.value).subscribe(()=>{
        this.router.navigate(['menu-exames'])
        location.reload();
    }) 
   
  }

  back(){
    this.router.navigate(['menu-exames'])
  }


}
